import * as THREE from 'three';
import {SpeechBubble} from './SpeechBubble';
import { MainCharacter } from './Character';
import { ThirdPersonCamera } from './ThirdPersonCamera';
import {CombineControllerInput} from './CombineControllerInput';
import {Environment} from './Environment';

const PUBLIC_URL = `${process.env.PUBLIC_URL}/assets`;

export class KLSpace {

  private _renderer:THREE.WebGLRenderer;
  private _scene:THREE.Scene;
  private _thirdPersonCamera:ThirdPersonCamera;
  private mainCharacter:MainCharacter;
  private otherCharacter1:MainCharacter;
  private _previousRAF:number = null;
  private _domRoot:any;
  private speechBubble:SpeechBubble;
  private environment:Environment;
  private combineControllerInput:CombineControllerInput;
  constructor(domRoot:any) {
    this._domRoot=domRoot;
    this._Initialize();
  }

  addEnvironment(){
    this.environment = new Environment();
    this.environment.load(this._scene);
  }

  _Initialize() {
    this._renderer = new THREE.WebGLRenderer({
      antialias:true, 
      alpha: true
    });
    this._renderer.outputEncoding = THREE.sRGBEncoding;
    this._renderer.shadowMap.enabled = true;
    this._renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    //this._renderer.setPixelRatio(window.devicePixelRatio);
    this._renderer.setSize(window.innerWidth, window.innerHeight);

    this._domRoot.append(this._renderer.domElement);

    this._scene = new THREE.Scene();

    this.addLights();
    this.addSky();
    this.addGround();
    this.addDialoag();
    this.addCharacter();
    this.addEnvironment();
    //this.addRaycast();

    this._RAF();

    (window as any).onresize = this._OnWindowResize;
  }

  addDialoag(){
    this.speechBubble = new SpeechBubble(this._scene, 33, "Hey, Welcome to Kholand.", 10);
		this.speechBubble.mesh.position.set(3, 23, 0);
  }

  addGround(){
    const plane = new THREE.Mesh(
        new THREE.PlaneGeometry(100, 100, 10, 10),
        new THREE.MeshStandardMaterial({
            color: 0x90ee90,
          }));
    plane.castShadow = false;
    plane.receiveShadow = true;
    plane.rotation.x = -Math.PI / 2;
    this._scene.add(plane);
  }

  addSky(){
    const loader = new THREE.CubeTextureLoader();
    const texture = loader.load([
      `${PUBLIC_URL}/images/skybox-2/right.png`,
      `${PUBLIC_URL}/images/skybox-2/left.png`,
      `${PUBLIC_URL}/images/skybox-2/top.png`,
      `${PUBLIC_URL}/images/skybox-2/bottom.png`,
      `${PUBLIC_URL}/images/skybox-2/front.png`,
      `${PUBLIC_URL}/images/skybox-2/back.png`,
    ]);
    texture.encoding = THREE.sRGBEncoding;
    this._scene.background = texture;
  }

  addLights(){
    const light = new THREE.DirectionalLight(0xFFFFFF, 1.0);
    light.position.set(-100, 100, 100);
    light.target.position.set(0, 0, 0);
    light.castShadow = true;
    light.shadow.bias = -0.001;
    light.shadow.mapSize.width = 4096;
    light.shadow.mapSize.height = 4096;
    light.shadow.camera.near = 0.1;
    light.shadow.camera.far = 500.0;
    light.shadow.camera.near = 0.5;
    light.shadow.camera.far = 500.0;
    light.shadow.camera.left = 50;
    light.shadow.camera.right = -50;
    light.shadow.camera.top = 50;
    light.shadow.camera.bottom = -50;
    this._scene.add(light);

    const ambientLight = new THREE.AmbientLight(0xFFFFFF, 0.25);
    this._scene.add(ambientLight);
  }

  addCharacter() {
    this.mainCharacter = new MainCharacter(this._scene, 2, new THREE.Vector3());
    const otherPosition = new THREE.Vector3(0,0,20);
    this.otherCharacter1 = new MainCharacter(this._scene, 1, otherPosition);
    this.combineControllerInput = new CombineControllerInput(this.mainCharacter);
    this._thirdPersonCamera = new ThirdPersonCamera(this.mainCharacter, window.innerWidth, window.innerHeight);
  }

  _OnWindowResize() {
    this._thirdPersonCamera && this._thirdPersonCamera.resize(window.innerWidth, window.innerHeight);
    this._renderer && this._renderer.setSize(window.innerWidth, window.innerHeight);
  }

  _RAF() {
    requestAnimationFrame((t) => {
      if (this._previousRAF === null) {
        this._previousRAF = t;
      }

      this._RAF();

      this._renderer.render(this._scene, this._thirdPersonCamera.getThreeCamera());
      this._Step(t - this._previousRAF);
      this._previousRAF = t;
    });
  }

  _Step(timeElapsed) {
    const timeElapsedS = timeElapsed * 0.001;
    if (this.mainCharacter) {
      this.mainCharacter.Update(timeElapsedS);
    }

    // if (this.otherCharacter1) {
    //   this.otherCharacter1.Update(timeElapsedS);
    // }

    if (this.speechBubble) {
        const postion = this.otherCharacter1.Position;
        const lookAt = this._thirdPersonCamera.getThreeCamera().position;
        this.speechBubble.Update(postion, lookAt);
    }
    this._thirdPersonCamera.Update(timeElapsedS);
  }

  addRaycast(){
    document.body.appendChild(this._renderer.domElement);
    document.addEventListener('touchend', onDocumentTouchEnd, false);
    document.addEventListener( 'mousemove', onPointerMove );

    const scope = this;
    const camera = scope._thirdPersonCamera.getThreeCamera();

    const pointer = new THREE.Vector2();

    function performRaycast(cord:THREE.Vector2){
      const raycaster = new THREE.Raycaster(  );
      raycaster.setFromCamera(cord, camera);
      const intersects = raycaster.intersectObjects(scope._scene.children, true );
      console.log("intersects", intersects);
    }

    function onPointerMove( event ) {
      pointer.x = ( event.clientX / window.innerWidth ) * 2 - 1;
      pointer.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
      performRaycast(pointer);
    }

    function onDocumentTouchEnd(event) {
        event.preventDefault();
        pointer.x = (event.changedTouches[0].clientX / window.innerWidth) * 2 - 1;
        pointer.y = -(event.changedTouches[0].clientY / window.innerHeight) * 2 + 1;
        performRaycast(pointer);
    }
  }
}

